// ** React Imports
import { Fragment, useState, useEffect, memo } from 'react'
import UiLoader from '@components/ui-loader'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
// ** Table Columns

// ** Store & Actions
import { adDelete, getADData, editAdData, adToggleStatus } from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
import { useHistory } from 'react-router-dom'
import BreadCrumbs from '@components/breadcrumbs'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'

// ** Third Party Components
import Select from 'react-select'
// import { selectThemeColors } from '@utils'
import Moment from "moment"
import ReactPaginate from 'react-paginate'
import { ChevronDown, Edit, MoreVertical, Trash2, Filter } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, Input, CustomInput, Label, Row, Col, DropdownToggle, DropdownMenu, DropdownItem, UncontrolledDropdown, CardHeader } from 'reactstrap'
// import AppCollapse from '@components/app-collapse'
import AppCollapse from '../../../../@core/components/app-collapse'

const DataTableTransaction = () => {

  const typeOptions = [
    { value: '1', label: 'Image' },
    { value: '2', label: 'Video' }
  ]

  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.ads)
  const history = useHistory()

  // ** States
  const MySwal = withReactContent(Swal)
  const [currentPage, setCurrentPage] = useState(1)
  const [searchValue, setSearchValue] = useState('')
  const [filteredData, setFilteredData] = useState([])
  const [status, setStatus] = useState(false)
  const [searchType, setSearchType] = useState("")
  const [dataPerPage, setDataPerPage] = useState('')

  useEffect(() => {
    dispatch(getADData(currentPage, dataPerPage))
  }, [currentPage, store.toggle_data])
  console.log(filteredData)

  useEffect(() => {
    setCurrentPage(1)
    dispatch(getADData(1, dataPerPage))
  }, [dataPerPage])

  // ** Get data on mount
  // ** Function to handle filter
  const handleFilter = e => {
    const value = e.target.value
    let updatedData = []
    setSearchValue(value)
    if (value.length) {
      updatedData = store.adData?.data?.data?.filter(item => {
        const startsWith =
          item.name?.toLowerCase().startsWith(value.toLowerCase()) 

        const includes =
          item.name?.toLowerCase().includes(value.toLowerCase()) 

        if (startsWith) {
          return startsWith
        } else if (!startsWith && includes) {
          return includes
        } else return null
      })
      setFilteredData(updatedData)
    } else {
      setFilteredData([])
    }
  }

  const editHandler = (id) => {
    history.push(`/manage_ad/edit/${id}`)
    dispatch(editAdData(id))
  }

  const SwitchClick = (id) => {
    setStatus(!status)
    dispatch(adToggleStatus(id, status))
  }

  const handleConfirmText = (id) => {
    console.log(id)
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ml-1'
      },
      buttonsStyling: false
    }).then(function (result) {

      if (result.value) {
        dispatch(adDelete(id))
        setCurrentPage(1)
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Your file has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const columns = [
    {
      name:'#',
      minWidth:'10px',
      cell : (row, pos) => `#${store.adData?.data?.from + pos}`
    },
    {
      name: 'Name',
      minWidth: '20px',
      selector: 'name',
      sortable: true,
      cell: (row) => row.name
    },
    {
      name: 'Type',
      minWidth: '20px',
      selector: 'type',
      sortable: true,
      cell: (row) => { return row.type === 1 ? 'Video' : row.type === 2 ? 'Image' : row.type === 3 ? 'Gif' : '-' }
    },
    {
      name: 'Expiry Date',
      minWidth: '20px',
      selector: 'expiry_date',
      sortable: true,
      cell: (row) => { return Moment((row.expiry_date), 'YYYY-MM-DD').format("DD MMM YYYY") }
    },
    {
      name: 'Redirecting Screen',
      minWidth: '20px',
      selector: 'url',
      sortable: true,
      cell: (row) => <a href={row?.cta?.url} target={`_blank`}>{row.cta.url ? row.cta.url : '-'}</a>
    },
    {
      name: 'Frequency',
      minWidth: '20px',
      selector: 'playing_time',
      sortable: true,
      cell: (row) => row.playing_time
    },
    {
      name: 'Status',
      minWidth: '138px',
      selector: 'active',
      sortable: true,
      cell: (row) => (
        <div className="column-action">
          {
            (
              <CustomInput
                className='custom-control-success'
                type='switch'
                id={`success${row.id}`}
                name='success'
                onChange={() => SwitchClick(row.id)}
                inline
                defaultChecked={row.status !== 0}
              />
            )
          }
        </div>
      )
    },
    {
      name: 'Action',
      minWidth: '138px',
      selector: 'active',
      cell: (row) => (
        <UncontrolledDropdown>
          <DropdownToggle className='pr-1' tag='span'>
            <MoreVertical size={15} />
          </DropdownToggle>
          <DropdownMenu right>
            <DropdownItem className="cursor-pointer w-100" title="Edit Feed" onClick={() => editHandler(row.id)}  >
              <Edit width="17" />
              <span className='align-middle ml-50'>Edit</span>
            </DropdownItem>
            <DropdownItem className="cursor-pointer w-100" title="Add Feed" onClick={() => handleConfirmText(row.id)} >
              <Trash2 width="17" />
              <span className='align-middle ml-50'>Delete</span>
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
      )
    }
  ]
  // ** Function to handle Pagination and get data

  const handlePagination = page => {
    setCurrentPage(page.selected + 1)
  }

  const typeHandler = (value) => {
    setSearchType(value)
    setCurrentPage(1)
  }

  // ** Function to handle per page

  console.log(store?.adData?.data?.last_page)

  // ** Custom Pagination
  const CustomPagination = () => {

    const count = store?.adData?.data?.data?.length ? store.adData?.data?.last_page : 1
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        breakLabel='...'
        pageCount={count}
        marginPagesDisplayed={2}
        pageRangeDisplayed={2}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        breakClassName='page-item'
        breakLinkClassName='page-link'
        containerClassName={
          'pagination react-paginate separated-pagination pagination-sm justify-content-end pr-1 mt-1'
        }
      />
    )
  }
  console.log(store?.adData)
  // ** Table data to render
  const dataToRender = () => {
    if (searchValue && filteredData.length > 0) {
      return filteredData
    } else if (searchValue || filteredData.length > 0) {
      return []
    } else if (store?.adData?.data?.data?.length > 0) {
      return store?.adData?.data?.data
    } else {
      return []
    }
  }

  if (store?.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  const data = [
    {
      title: <div><Filter size={18} /> Filter</div>,
      content: (
        <>
          <Row className=''>            
            <Col>
              <Label className='mr-1' for='search-input'>
                Search
              </Label>
              <Input
                className='dataTable-filter'
                type='text'
                bsSize='sm'
                id='search-input'
                value={searchValue}
                onChange={(e) => handleFilter(e)}
                placeholder='search...'
              />
            </Col>
          </Row>
          <Row>
          <Col className='mb-1 mt-1' md='6' sm='12'>
              <Label>Type:</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                onChange={(e) => typeHandler(e?.label)}
                // onChange={e =>  setSearchType(e?.label)}
                // theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                options={typeOptions}
                placeholder='search AD type...'
                isClearable={true}
              />
            </Col>
          </Row>
        </>
      )
    }
  ]

  console.log(store)
  console.log(dataPerPage)
  console.log(searchType)
  console.log(filteredData)
  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='AD ' breadCrumbActive='List' />
      <AppCollapse data={data} type='border' accordion className="mb-1" />
      <Card data-testid='test_id'>
        <CardHeader>
        <Col sm='6'>
              <div className='d-flex align-items-center'>
                <Label for='sort-select'>show</Label>
                <Input
                  className='dataTable-select'
                  type='select'
                  id='sort-select'
                  value={dataPerPage}
                  onChange={e => setDataPerPage(e.target.value)}
                >
                  <option value={10}>10</option>
                  <option value={50}>50</option>
                  <option value={100}>100</option>
                </Input>
                <Label for='sort-select'>entries</Label>
              </div>
            </Col>
        </CardHeader>
        <DataTable
          noHeader
          // pagination
          paginationServer
          className='react-dataTable'
          columns={columns}
          sortIcon={<ChevronDown size={10} />}
          paginationComponent={CustomPagination}
          data={dataToRender()}
        />
      </Card>
      <CustomPagination />
    </Fragment>
  )
}

export default memo(DataTableTransaction)